import React from "react";
import { homeIcon } from "../../assets/icons/fontIcons";
import { Cell } from "../../components/cell/cell";
import { AppContext } from "../../components/context/appContext";
import { Fade } from "../../components/fade/fade";
import { Grid } from "../../components/grid/grid";
import { TactileButton } from "../../components/tactileButton/tactileButton";
import { initGrid } from "../../util/gameUtils";
import { joinClassesConditionally } from "../../util/utils";
import "./tutorialScreen.scss";

// [org, sol]
const tutorialGridScript = [
    ["NNNN", "0120"],
];

export const TutorialScreen: React.FC = () => {
    const appContext = React.useContext(AppContext);

    const [tutorialStep, setTutorialStep] = React.useState(0);
    const [gridData, setGridData] = React.useState(initGrid(tutorialGridScript[tutorialStep][0]))

    return <Fade>
        <div
            id="tutorial-screen"
            className={joinClassesConditionally([
                ["screen", true],
            ])}
        >
            <Grid height={1} width={1} gridData={gridData} setGridData={setGridData} />

            <span className="tutorial-text">
                All cells must be <span className="tutorial-text-colored">colored</span>.
            </span>


            <TactileButton
                className="home-button"
                onClick={() => appContext.setPage("home")}
            >{homeIcon}</TactileButton>
        </div>
    </Fade>
}